//     return null;
// }
class CreateStockItemTransaction extends PureComponent<IProps> {
  render() {
    const { stockItemId, id } = this.props.match.params
    return (
      <Query query={STOCK_SUPPLIER_NAMES}>
        {({ loading, error, data }: IGraphqlData) => {
          const defaults = {
            supplier: {
              type: 'select',
              default: data.stockSuppliers,
              mapping: { key: 'name', value: 'id' },
            },
            quantity: {
              type: 'number',
              default: 0,
              min: 0,
              step: '1',
            },
            type: {
              type: 'select',
              default: [
                { key: 'DEBIT', value: '0' },
                { key: 'CREDIT', value: '1' },
              ],
              current: '0',
            },
          }
          return (
            <Form
              {...this.props}
              query={STOCK_ITEM_TRANSACTION}
              defaults={defaults}>
              {({ update, defaults }: IFormProps) => {
                return (
                  <FormGenerator
                    {...this.props}
                    defaults={defaults}
                    mutation={update ? UPDATE_STOCK_ITEM_TRANSACTION : CREATE_STOCK_ITEM_TRANSACTION}
                    title={!update ? 'Create a new stock item transaction' : 'Update this stock item transaction'}
                    submitText={!update ? 'Create' : 'Update'}
                    mutateFunc={(fn, data) => fn({ variables: { stockItemId, id, data } })}
                  >
                  </FormGenerator>
                )
              }}
            </Form>
          )
        }}

      </Query>
    )
  }
}

export default CreateStockItemTransaction
